import { Button, DashboardIcon, EmailIcon, ExploreIcon, HelpIcon, HomeIcon, LoginIcon, LogoutIcon, SettingsIcon, IMobileView } from "./imports";

export default function MobileView({ user, OnLogOut, OnMobileViewClick }: IMobileView) {
    const sharedButtonClassName = `w-[75%] flex justify-center items-center rounded-full shadow-lg bg-gray-100 shadow-gray-400 m-2 p-4 cursor-pointer hover:scale-110 ease-in duration-200`;
    const sharedSpanClassName = "pl-4";

    const handleLogoutViewClick = () => {
        OnMobileViewClick();
        OnLogOut();
    };

    return (
        <div className="fixed w-full h-screen bg-white/40 flex flex-col justify-center items-center z-20 lg:hidden md:flex">
            {/* Home */}
            <Button
                linkPath="/"
                linkText="Home"
                Icon={HomeIcon}
                iconSize="small"
                spanClassName={sharedSpanClassName}
                OnMobileViewClick={OnMobileViewClick}
                buttonClassName={sharedButtonClassName}
            />

            {/* Explore */}
            <Button
                iconSize="small"
                linkText="Explore"
                Icon={ExploreIcon}
                linkPath="/explore"
                spanClassName={sharedSpanClassName}
                OnMobileViewClick={OnMobileViewClick}
                buttonClassName={sharedButtonClassName}
            />

            {/* Dashboard */}
            {user && (
                <Button
                    iconSize="small"
                    linkText="Dashboard"
                    Icon={DashboardIcon}
                    linkPath="/dashboard"
                    spanClassName={sharedSpanClassName}
                    OnMobileViewClick={OnMobileViewClick}
                    buttonClassName={sharedButtonClassName}
                />
            )}

            {/* Support */}
            <Button
                linkText="Help"
                Icon={HelpIcon}
                iconSize="small"
                linkPath="/support"
                spanClassName={sharedSpanClassName}
                OnMobileViewClick={OnMobileViewClick}
                buttonClassName={sharedButtonClassName}
            />

            {/* Contact Us */}
            <Button
                iconSize="small"
                Icon={EmailIcon}
                linkText="Contact Us"
                linkPath="/contact-us"
                spanClassName={sharedSpanClassName}
                OnMobileViewClick={OnMobileViewClick}
                buttonClassName={sharedButtonClassName}
            />

            {/* Account Settings*/}
            {user && (
                <Button
                    iconSize="small"
                    linkText="Account"
                    Icon={SettingsIcon}
                    linkPath="/my-account"
                    spanClassName={sharedSpanClassName}
                    OnMobileViewClick={OnMobileViewClick}
                    buttonClassName={sharedButtonClassName}
                />
            )}

            {/* Conditional renders*/}
            {user ? (
                <>
                    {/* Log Out*/}
                    <Button
                        linkPath="/"
                        iconSize="small"
                        Icon={LogoutIcon}
                        linkText="Log out"
                        spanClassName={sharedSpanClassName}
                        buttonClassName={sharedButtonClassName}
                        OnMobileViewClick={handleLogoutViewClick}
                    />
                </>
            ) : (
                <>
                    {/* Log In */}
                    <Button
                        iconSize="small"
                        Icon={LoginIcon}
                        linkPath="/login"
                        linkText="Log in"
                        spanClassName={sharedSpanClassName}
                        OnMobileViewClick={OnMobileViewClick}
                        buttonClassName={sharedButtonClassName}
                    />
                </>
            )}
        </div>
    );
}
