import { db, setDoc, doc, collection } from "./config";
import { v4 as uuidv4 } from "uuid";

interface IAddNewClientUser {
    uid: string;
    email: string | null;
    emailVerified: boolean;
    displayName: string | null;
    setUserRole: (role: string) => void;
    setUserEmail: (email: string | null) => void;
    setUserIsVerified: (verified: boolean) => void;
    setUserUsername: (username: string | null) => void;
}

export default async function AddNewClientUser({
    uid,
    email,
    setUserRole,
    displayName,
    setUserEmail,
    emailVerified,
    setUserUsername,
    setUserIsVerified,
}: IAddNewClientUser) {
    const role = "standard";

    try {
        // Generate a random playlist ID
        const playlistID = uuidv4();

        // Create the user document
        await setDoc(doc(db, "users", uid), {
            email,
            username: displayName,
            role: role,
            isVerified: emailVerified,
            videosPlaylistID: playlistID, // Store the generated Playlist ID
        });

        // Create a default playlist document in the 'playlists' subcollection
        const playlistDocRef = doc(collection(db, "users", uid, "playlists"), playlistID);
        await setDoc(playlistDocRef, {
            owner: uid,
            public: true,
            title: "My Playlist",
            videos: [],
        });

        // Create a publicPlaylists collection pointing to the playlist and owner uid
        await setDoc(doc(db, "publicPlaylists", playlistID), {
            owner: uid,
            public: true,
            videosPlaylistID: playlistID,
            createdAt: new Date().toISOString(),
        });

        // Update user context
        setUserRole(role);
        setUserEmail(email);
        setUserUsername(displayName);
        setUserIsVerified(emailVerified);

        return {
            success: true,
            error: null,
        };
    } catch (error) {
        return {
            success: false,
            error: (error as Error).message,
        };
    }
}
