import { User } from "firebase/auth";
import Link from "next/link";

export default function AuthButtons({ onLogout, user }: { user: User | null; onLogout: () => void }) {
    const sharedClassName = `bg-gradient-to-r from-gray-300 to-teal-400 hover:from-orange-50 hover:to-pink-500 text-white py-2 px-4 border rounded-md cursor-pointer`;
    const buttonName = user ? "Log out" : "Log in";
    const linkPath = user ? "/" : "/login";

    const onClickEvent = async () => {
        if (user) onLogout();
    };

    return (
        <div className="hidden md:flex items-center justify-between mr-10">
            <div className="flex items-center space-x-4">
                <Link href={linkPath} className={sharedClassName} onClick={onClickEvent}>
                    <span>{buttonName}</span>
                </Link>
            </div>
        </div>
    );
}
