import Link from "next/link";
import LogoIcon from "@/public/wavyLines";

export default function LogoAndTitle({ linkPath, title }: { linkPath: string; title: string }) {
    return (
        <Link href={linkPath}>
            <div className="flex items-center flex-shrink-0">
                <LogoIcon className="h-15 w-16 mr-5" />
                <span className="text-xl tracking-tight hover:cursor-pointer onclick">{title}</span>
            </div>
        </Link>
    );
}
