import { Player } from "@/app/utils";
import { arrayUnion } from "firebase/firestore";
import { db, doc, getDoc, updateDoc } from "./config";

export default async function addVideoToPlaylist(uid: string, video: Player) {
    try {
        // Get the playlist ID from the user document
        const userDocRef = doc(db, "users", uid);
        const userDocSnapshot = await getDoc(userDocRef);
        if (!userDocSnapshot.exists()) throw new Error("User document does not exist");

        const userData = userDocSnapshot.data();
        const playlistID = userData?.videosPlaylistID;
        if (!playlistID) throw new Error("Playlist ID is missing");

        // Fetch the playlist subcollection and it's values
        const playlistDocRef = doc(db, "users", uid, "playlists", playlistID);
        const playlistDocSnapshot = await getDoc(playlistDocRef);

        if (!playlistDocSnapshot.exists()) {
            throw new Error("Playlist does not exist");
        }
        // Check if the video already exists in the playlist
        const playlistData = playlistDocSnapshot.data();
        const existingVideos = playlistData?.videos || [];

        const videoExists = existingVideos.some((existingVideo: Player) => existingVideo?.videoID === video?.videoID);
        if (videoExists) {
            return {
                success: false,
                error: "Video already exists in the playlist",
                playlistID,
            };
        }

        // Update the playlist
        await updateDoc(playlistDocRef, {
            videos: arrayUnion(video),
        });

        return {
            success: true,
            error: null,
            playlistID,
        };
    } catch (error) {
        return {
            success: false,
            error: (error as Error).message,
            playlistID: null,
        };
    }
}
