import { User } from "firebase/auth";
import AuthButtons from "./AuthButtons";
import LogoAndTitle from "./LogoAndTitle";

export default function DesktopView({ user, onLogout }: { user: User | null; onLogout: () => void }) {
    return (
        <div>
            <nav className="fixed top-0 z-50 w-full py-3 bg-opacity-90 shadow-md backdrop-blur-md border-b bg-white-100/80">
                <div className="px-4 mx-auto relative text-sm">
                    <div className="flex justify-between items-center">
                        <LogoAndTitle linkPath="/" title="Virtual" />
                        
                        <AuthButtons onLogout={onLogout} user={user} />
                    </div>
                </div>
            </nav>
        </div>
    );
}
