import { useState } from "react";
import MobileView from "./MobileView";
import { useUser } from "@/app/store";
import DesktopView from "./DesktopView";
import { useLogout } from "@/app/firebase";
import MenuIcon from "@mui/icons-material/Menu";

export default function NavigationBar() {
    const [nav, setNav] = useState(false);
    const toggleNavigationBar = () => setNav(!nav);
    const { user, setUserRole, setUserIsVerified } = useUser();

    const handleLogout = async () => {
        // update context
        setUserRole(null);
        setUserIsVerified(null);
        await useLogout();
    };

    return (
        <div>
            {/* Mobile and Tablet Hamburger Menu Icon */}
            <MenuIcon
                onClick={toggleNavigationBar}
                sx={{
                    position: "fixed",
                    top: "3%",
                    right: "5%",
                    zIndex: 99,
                    display: { md: "none", sm: "flex" },
                    cursor: "pointer",
                }}
            />

            {/* Mobile View - Shown when nav is true */}
            {nav && <MobileView OnMobileViewClick={toggleNavigationBar} user={user} OnLogOut={handleLogout} />}

            {/* Desktop View - Shown by default */}
            <DesktopView onLogout={handleLogout} user={user} />
        </div>
    );
}
