import { setLogLevel } from "firebase/firestore";
import { db, setDoc, doc, getDoc, deleteDoc } from "./config";

export default async function storeGeneratedCode(email: string, code: string, expirationDate: number) {
    setLogLevel("info");
    try {
        const sanitizedEmail = email.replace(/[^a-zA-Z0-9]/g, "_");
        await setDoc(doc(db, "verification_codes", sanitizedEmail), {
            code,
            isUsed: false,
            isExpired: false,
            expirationDate,
        });

        return {
            success: true,
            code,
            error: null,
        };
    } catch (error) {
        return {
            success: false,
            error: (error as Error).message,
            code: null,
        };
    }
}

export async function isCodeValid(email: string, inputCode: string) {
    let verificationCode;
    try {
        const sanitizedEmail = email.replace(/[^a-zA-Z0-9]/g, "_");
        const docRef = doc(db, "verification_codes", sanitizedEmail);
        const docSnap = await getDoc(docRef);

        // If document does not exist, return valid false
        if (!docSnap.exists()) {
            return {
                valid: false,
                success: false,
                error: "Code is expired or already used",
            };
        }

        const data = docSnap.data();
        const { code, isUsed, expirationDate } = data;
        verificationCode = code;

        // Check if the code has expired or is already used
        const expired = Date.now() > expirationDate;
        const used = isUsed;

        // If expired or used, delete the document and return valid: false
        if (expired || used) {
            await deleteDoc(docRef);
            return {
                valid: false,
                success: false,
                error: "Code is expired or already used",
            };
        }

        // Check if the input code matches the stored code
        const validCode = inputCode === code;
        if (!validCode) {
            return {
                valid: false,
                success: false,
                error: "Invalid code",
            };
        }

        // If everything is valid, return valid as true and mark the code as used
        await deleteDoc(docRef);
        return {
            valid: true,
            success: true,
            error: null,
        };
    } catch (error) {
        return {
            valid: false,
            success: false,
            error: (error as Error).message,
        };
    }
}
