import { auth } from "../config";
import { signOut } from "firebase/auth";

export default async function useLogout() {
    try {
        await signOut(auth);
        return {
            success: true,
            error: null,
            user: null,
        };
    } catch (error) {
        return {
            success: false,
            error: (error as Error).message,
        };
    }
}