import { create } from "zustand";
import { Player } from "@/app/utils";
import { User } from "firebase/auth";
import { fetchPlaylist, removeVideoFromPlaylist } from "@/app/firebase";

interface MediaState {
    videos: Player[];
    isLoading: boolean;
    playlistID: string;
    error: string | null;
    playlistTitle: string;
    playlistVideos: Player[];
    queueVideos: Player[];
    currentVideo: Player | null;
    loopEnabled: boolean;
    videoIsEnded: boolean;
    isPlaylistActive: boolean;

    // Playlist Methods
    clearPlaylist: () => void;
    addToPlaylist: (video: Player) => void;
    togglePlaylist: (isActive: boolean) => void;
    removeFromPlaylist: (video: Player, userID: string) => Promise<void>;
    setPlaylistVideos: (videos: Player[]) => void;
    fetchPlaylistData: (user: User | null) => Promise<void>;

    // Queue Methods
    clearQueue: () => void;
    addToQueue: (video: Player) => void;
    removeFromQueue: (video: Player) => void;

    // Player Methods
    addVideo: (video: Player) => void;
    setVideos: (Videos: Player[]) => void;
    clearVideos: () => void;
    toggleLoop: (enabled: boolean) => void;
    setCurrentVideo: (video: Player) => void;
    setVideoIsEnded: (ended: boolean) => void;
    playNextVideo: (
        setTiktokVideo: (tiktokVideo: Player) => void,
        setYoutubeVideo: (youtubeVideo: Player) => void,
        setSoundCloudVideo: (soundCloudVideo: Player) => void,
        setSliderValue: (value: [number, number]) => void,
        isPlaylistActive: boolean,
        playlistID: string | undefined,
        createSearchParams: (params: Record<string, string>) => void
    ) => void;
    playSelectedVideo: (
        index: number,
        setTiktokVideo: (tiktokVideo: Player) => void,
        setYoutubeVideo: (youtubeVideo: Player) => void,
        setSoundCloudVideo: (soundCloudVideo: Player) => void,
        setSliderValue: (value: [number, number]) => void,
        isPlaylistActive: boolean,
        playlistID: string | undefined
    ) => void;
    removeVideo: (video: Player) => void;
}

export const useMedia = create<MediaState>((set, get) => ({
    error: null,
    isLoading: false,
    queueVideos: [],
    playlistVideos: [],
    currentVideo: null,
    loopEnabled: false,
    videoIsEnded: false,
    playlistID: "",
    playlistTitle: "",
    isPlaylistActive: false,

    videos: [],
    removeVideo: (video) =>
        set((state) => ({
            videos: state.videos.filter((v) => v?.videoID !== video?.videoID),
        })),

    addVideo: (video) =>
        set((state) => ({
            videos: [...state.videos, video],
        })),
    setVideos: (Videos: Player[]) => set({ videos: Videos }),
    clearVideos: () => set({ videos: [] }),

    setCurrentVideo: (video) => set({ currentVideo: video }),
    setPlaylistVideos: (videos: Player[]) => set({ playlistVideos: videos }),

    toggleLoop: (enabled: boolean) => set({ loopEnabled: enabled }),

    addToQueue: (video) =>
        set((state) => ({
            queueVideos: [...state.queueVideos, video],
        })),
    removeFromQueue: (video) =>
        set((state) => ({
            queueVideos: state.queueVideos.filter((v) => v?.videoID !== video?.videoID),
        })),
    clearQueue: () => set({ queueVideos: [] }),

    addToPlaylist: (video) =>
        set((state) => ({
            playlistVideos: [...state.playlistVideos, video],
        })),
    removeFromPlaylist: async (video, userID) => {
        const result = await removeVideoFromPlaylist(userID, video);

        if (result.success) {
            set((state) => ({
                playlistVideos: state.playlistVideos.filter((v) => v?.videoID !== video?.videoID),
            }));
        } else {
            console.error(result.error);
        }
    },
    clearPlaylist: () => set({ playlistVideos: [] }),

    togglePlaylist: (isActive) => set({ isPlaylistActive: isActive }),

    setVideoIsEnded: (ended) => set({ videoIsEnded: ended }),

    playNextVideo: (setTiktokVideo, setYoutubeVideo, setSoundCloudVideo, setSliderValue, isPlaylistActive, playlistID, createSearchParams) => {
        const { videos, currentVideo, setCurrentVideo } = get();

        console.log("Active Videos:", videos); // Check active videos

        console.log(currentVideo);
        if (!currentVideo || videos.length === 0) return;

        const currentIndex = videos.findIndex((video) => video?.videoID === currentVideo.videoID);
        const nextIndex = (currentIndex + 1) % videos.length;
        const nextVideo = videos[nextIndex];

        console.log("Current Video:", currentVideo); // Check current video
        console.log("Next Video:", nextVideo); // Check next video

        if ((nextVideo && nextVideo.platform === "youtube") || (nextVideo && nextVideo.url.includes("youtube"))) {
            setTiktokVideo(null);
            setSoundCloudVideo(null);
            setYoutubeVideo({ ...nextVideo, index: nextIndex.toString() });
            setCurrentVideo({ ...nextVideo, index: nextIndex.toString() });
            console.log("Current video set to:", nextVideo); // Log current video after setting

            const { start, end } = nextVideo;
            setTimeout(() => {
                if (start && end) {
                    setSliderValue([parseInt(start), parseInt(end)]);
                }
            }, 500);
        } else if ((nextVideo && nextVideo.platform === "tiktok") || (nextVideo && nextVideo.url.includes("tiktok"))) {
            console.log("Setting TikTok video:", nextVideo.videoID); // Log next TikTok video
            setYoutubeVideo(null);
            setSoundCloudVideo(null);
            setTiktokVideo({ ...nextVideo, index: nextIndex.toString() });
            setCurrentVideo({ ...nextVideo, index: nextIndex.toString() });
            console.log("Current video set to:", nextVideo); // Log current video after setting

            // const index = nextVideo?.index?.toString();
            // if (playlistID && index) {
            //     const newSearchParams: Record<string, string> = {
            //         tVideoID: currentVideo.videoID,
            //         list: playlistID,
            //         index,
            //     };
            //     console.log(newSearchParams);
            // }
        } else if ((nextVideo && nextVideo.platform === "soundcloud") || (nextVideo && nextVideo.url.includes("soundcloud"))) {
            setYoutubeVideo(null);
            setTiktokVideo(null);
            setSoundCloudVideo({ ...nextVideo, index: nextIndex.toString() });
            setCurrentVideo({ ...nextVideo, index: nextIndex.toString() });

            const { start, end } = nextVideo;
            setTimeout(() => {
                if (start && end) {
                    setSliderValue([parseInt(start), parseInt(end)]);
                }
            }, 500);
        }
    },

    playSelectedVideo: (index, setTiktokVideo, setYoutubeVideo, setSoundCloudVideo, setSliderValue, isPlaylistActive, playlistID) => {
        const { playlistVideos, queueVideos, currentVideo, setCurrentVideo } = get();

        const activeVideos = isPlaylistActive ? playlistVideos : queueVideos;
        console.log("Active Videos:", activeVideos); // Check active videos

        console.log(currentVideo);
        if (!currentVideo || activeVideos.length === 0 || index < 0 || index >= activeVideos.length) return;

        const selectedVideo = activeVideos[index]; // Get the selected video based on the provided index
        console.log("Selected Video:", selectedVideo); // Log the selected video

        if (selectedVideo) {
            // Check the platform of the selected video
            if (selectedVideo.platform === "youtube" || selectedVideo.url.includes("youtube")) {
                setTiktokVideo(null);
                setSoundCloudVideo(null);
                setYoutubeVideo({ ...selectedVideo, index: index.toString() });
                setCurrentVideo({ ...selectedVideo, index: index.toString() });
                console.log("Current video set to:", selectedVideo); // Log current video after setting

                const { start, end } = selectedVideo;
                setTimeout(() => {
                    if (start && end) {
                        setSliderValue([parseInt(start), parseInt(end)]);
                    }
                }, 500);
            } else if (selectedVideo.platform === "tiktok" || selectedVideo.url.includes("tiktok")) {
                console.log("Setting TikTok video:", selectedVideo.videoID); // Log next TikTok video
                setYoutubeVideo(null);
                setSoundCloudVideo(null);
                setTiktokVideo({ ...selectedVideo, index: index.toString() });
                setCurrentVideo({ ...selectedVideo, index: index.toString() });
                console.log("Current video set to:", selectedVideo); // Log current video after setting

                const newIndex = selectedVideo?.index?.toString();
                if (playlistID && newIndex) {
                    const newSearchParams: Record<string, string> = {
                        tVideoID: selectedVideo.videoID,
                        list: playlistID,
                        index: newIndex,
                    };
                    console.log(newSearchParams);
                    // Uncomment the next line if you want to create search params
                    // createSearchParams(newSearchParams);
                }
            } else if (selectedVideo.platform === "soundcloud" || selectedVideo.url.includes("soundcloud")) {
                setTiktokVideo(null);
                setYoutubeVideo(null);
                setSoundCloudVideo({ ...selectedVideo, index: index.toString() });
                setCurrentVideo({ ...selectedVideo, index: index.toString() });
                console.log("Current video set to:", selectedVideo); // Log current video after setting

                const { start, end } = selectedVideo;
                setTimeout(() => {
                    if (start && end) {
                        setSliderValue([parseInt(start), parseInt(end)]);
                    }
                }, 500);
            }
        } else {
            console.warn("No video found at the specified index."); // Log a warning if the video is not found
        }
    },

    fetchPlaylistData: async (user) => {
        if (!user?.uid) return;

        set({ isLoading: true, error: null });
        try {
            const result = await fetchPlaylist(user.uid);
            if (result.success && result.playlistData) {
                set({
                    playlistTitle: result.playlistData.title,
                    playlistVideos: result.playlistData.videos as Player[],
                    playlistID: result.playlistID,
                    isLoading: false,
                });
            } else {
                set({ error: result.error || "Failed to fetch playlist.", isLoading: false });
            }
        } catch (error) {
            set({ error: (error as Error).message, isLoading: false });
        }
    },
}));
