import { db, doc, getDoc } from "./config";

export default async function fetchPlaylistByID(playlistID: string) {
    try {
        const publicPlaylistsRef = doc(db, "publicPlaylists", playlistID);
        const publicPlaylistSnapshot = await getDoc(publicPlaylistsRef);
        if (!publicPlaylistSnapshot.exists()) throw new Error("Playlist is private or does not exist");

        const { owner } = publicPlaylistSnapshot.data() || {};
        if (!owner) throw new Error("Owner of the playlist could not be determined");

        // Fetch playlist from user's subcollection
        const playlistDocRef = doc(db, "users", owner, "playlists", playlistID);
        const playlistSnapshot = await getDoc(playlistDocRef);
        if (!playlistSnapshot.exists()) throw new Error("Playlist document does not exist");

        const playlistData = playlistSnapshot.data();

        // Ensure the playlist is public
        if (!playlistData?.public) throw new Error("Playlist is private");

        return {
            success: true,
            error: null,
            title: playlistData.title,
            videos: playlistData.videos,
            playlistID,
        };
    } catch (error) {
        return {
            success: false,
            error: (error as Error).message,
            title: null,
            videos: null,
            playlistID: null,
        };
    }
}
