import { create } from "zustand";
import { auth, db, doc, getDoc } from "@/app/firebase";
import { onAuthStateChanged, Unsubscribe, User } from "firebase/auth";

interface UserState {
    loading: boolean;
    user: User | null;
    role: string | null;
    code: string | null;
    email: string | null;
    username: string | null;
    isPlaylistActive: boolean;
    isVerified: boolean | null;
    codeIsUsed: boolean | null;
    codeIsExpired: boolean | null;
    setCode: (code: string | null) => void;
    subscribeToAuthState: () => Unsubscribe;
    setUserRole: (role: string | null) => void;
    togglePlaylist: (enabled: boolean) => void;
    setUserEmail: (email: string | null) => void;
    fetchUserData: (uid: string) => Promise<void>;
    setUserUsername: (username: string | null) => void;
    setCodeIsUsed: (codeIsUsed: boolean | null) => void;
    setUserIsVerified: (isVerified: boolean | null) => void;
    setCodeIsExpired: (codeIsExpired: boolean | null) => void;
}

export const useUser = create<UserState>((set) => ({
    user: null,
    role: null,
    code: null,
    email: null,
    loading: true,
    username: null,
    codeIsUsed: null,
    isVerified: null,
    codeIsExpired: null,
    isPlaylistActive: false,

    setCode: (code) => set({ code }),
    setUserRole: (role) => set({ role }),
    setUserEmail: (email) => set({ email }),
    setUserUsername: (username) => set({ username }),
    setCodeIsUsed: (codeIsUsed) => set({ codeIsUsed }),
    setUserIsVerified: (isVerified) => set({ isVerified }),
    setCodeIsExpired: (codeIsExpired) => set({ codeIsExpired }),
    togglePlaylist: (enabled) => set({ isPlaylistActive: enabled }),

    fetchUserData: async (uid) => {
        const userDocRef = doc(db, "users", uid);
        const userDocSnap = await getDoc(userDocRef);

        if (userDocSnap.exists()) {
            const data = userDocSnap.data();
            set({
                role: data.role || null,
                email: data.email || null,
                username: data.displayName || null,
                isVerified: data.isVerified || false,
            });
        }
    },

    // Initialize auth state
    subscribeToAuthState: () => {
        const unsubscribe = onAuthStateChanged(auth, (currentUser) => {
            set({ user: currentUser, loading: false });
            if (currentUser) {
                useUser.getState().fetchUserData(currentUser.uid);
                set({ isPlaylistActive: true }); // Set to true upon login
            } else {
                set({
                    role: null,
                    email: null,
                    username: null,
                    isVerified: null,
                    code: null,
                    codeIsUsed: null,
                    codeIsExpired: null,
                    isPlaylistActive: false, // Set to false when logged out
                });
            }
        });
        return unsubscribe;
    },
}));
