"use client";

import NavigationBar from "./NavigationBar/page";
import { usePathname } from "next/navigation";

export default function ClientLayout({ children }: { children: React.ReactNode }) {
    const path = usePathname();
    const excludeNavbarFromPaths = ["/login", "/signup"];
    const includesPath = excludeNavbarFromPaths.includes(path);

    // console.log('path: ', path)
    // console.log('exclude navbar from paths: ', excludeNavbarFromPaths)
    // console.log('includes path to exclude:', includesPath)

    return (
        <>
            {!includesPath && <NavigationBar />}
            {children}
        </>
    );
}
