import { create } from "zustand";
import { IPlayer, Player } from "@/app/utils";

export const usePlayer = create<IPlayer>((set, get) => ({
    duration: 0,
    currentTime: 0,
    tiktokVideo: null,
    youtubeVideo: null,
    sliderValue: [0, 0],
    soundCloudVideo: null,
    setDuration: (duration) => {
        set({ duration });
        const sliderValue = get().sliderValue; // Automatically reset slider to match new duration
        set({ sliderValue: [sliderValue[0], duration] });
    },
    setSliderValue: (value) => set({ sliderValue: value }),
    setYoutubeVideo: (video: Player) => {
        set({ youtubeVideo: video });
    },
    setTiktokVideo: (video: Player) => {
        set({ tiktokVideo: video });
    },
    setSoundCloudVideo: (video: Player) => {
        set({ soundCloudVideo: video });
    },
    resetSlider: () => {
        const duration = get().duration;
        set({ sliderValue: [0, duration] });
    },
}));
