import Link from "next/link";
import { IMobileViewButton } from "@/app/utils";

export default function Button({ OnMobileViewClick, buttonClassName, spanClassName, iconSize, linkPath, linkText, Icon }: IMobileViewButton) {
    return (
        <Link href={linkPath} className={buttonClassName} onClick={OnMobileViewClick}>
            <Icon fontSize={iconSize} />
            <button>
                <span className={spanClassName}>{linkText}</span>
            </button>
        </Link>
    );
}
